import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/Index'
import Signup from '../components/Signup'
import Fortune from '../components/Fortune'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'Index',
            component: Index
        },
        {
            path: '/signup',
            name: 'Signup',
            component: Signup
        },
        {
            path: '/fortune',
            name: 'Fortune',
            component: Fortune
        }
    ]
});
