<template>
  <div>
	<Head/>
	<Categories class="cate"/>
  </div>
</template>



<script>
// import Vue from 'vue';
import Head from "./Head.vue";
import PromptUser from "./PromptUser.vue";
import Categories from "./Categories.vue";


  export default{
	name:'index',
	created(){
		//show modal
		if(this.$store.getters.shownModal == false){
			this.showmodal();
		}else{
			console.log(this.$store.getters.shownModal);
		}
		
	},
	components:{
		Head, Categories, PromptUser,
	},
	data(){
		return{
			
		}
	},
	methods:{
		showmodal(){

			this.$store.dispatch('get_shown_modal', true);
			this.$modal.show(PromptUser);
			
		}
	}
  }
</script>

<style>
	.cate{
		z-index:-1;
		margin-top: -50px;
	}
</style>
