import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

//Instantiate Vuex instance
Vue.use(Vuex);

const state = {
    shownModal: false,
    spinnedOnce: false,
    validUser: false,
    showSignUp: false,
    userDetails: {

    },
    signInDetails: {

    }
}//end state

const mutations = {
    UPDATE_SHOW_SIGN_UP(state, payload) {
        state.showSignUp = payload;
    },
    UPDATE_SHOWN_MODAL(state, payload) {
        state.shownModal = payload;
    },
    UPDATE_SPINNED_ONCE(state, payload) {
        state.spinnedOnce = payload;
    },
    UPDATE_VALID_USER(state, payload) {
        state.validUser = payload;
    },
    UPDATE_USER_DETAILS(state, payload) {
        state.userDetails = payload;
    },
    UPDATE_SIGNIN_DETAILS(state, payload) {
        state.signInDetails = payload;
    }
}

const actions = {
    get_show_sign_up({ commit }, value) {
        commit('UPDATE_SHOW_SIGN_UP', value);
    },

    get_shown_modal({ commit }, value) {
        commit('UPDATE_SHOWN_MODAL', value);
    },

    get_spinned_once({ commit }, value) {
        commit('UPDATE_SPINNED_ONCE', value);
    },

    get_user_details({ commit }, value) {
        axios.get('api/post/user' + value)
            .then((response) => {
                //commit user details here
                commit('UPDATE_VALID_USER', response.user);
            })
            .catch(error => {
                console.log(error);
            });
    },

    get_signin_details({ commit }, value) {
        commit('UPDATE_SIGNIN_DETAILS', value);
    }
}

const getters = {
    shownModal: state => state.shownModal,
    spinnedOnce: state => state.spinnedOnce,
    validUser: state => state.validUser,
    userDetails: state => state.userDetails,
    signInDetails: state => state.signInDetails,
    showSignUp: state => state.showSignUp,
}

//Create & Export Store for Global Use
export default new Vuex.Store({
    state, mutations, actions, getters
});
